const helpers = (context) => {
  const filter = context.$filter;

  const getAddressProperties = (prefix) =>
    ['Line1', 'Line2', 'Line3', 'Line4'].map(prop => prefix + prop);

  const getAllProperties = (prefix, props) =>
    (props.includes('address') ? props.concat(getAddressProperties(prefix)) : props);

  const notEmptyFilter = (value) => !!value;

  return {
    onListClick: (ext, $ctrl) => {
      if (ext.selectedApproval) {
        $ctrl.selectApproval(ext.selectedApproval);
      }
    },

    showContactBlock: ($ctrl, contactProperties = []) =>
      getAllProperties('address', contactProperties)
        .some((prop) => !!$ctrl.state.approval.data.data[prop]),

    showAccountBlock: (account, accountProperties = []) =>
      getAllProperties('bankAddress', accountProperties).some((prop) => !!account[prop]),

    getContactAddress: (dataObj) => getAddressProperties('address')
      .map((prop) => dataObj[prop])
      .filter((address) => !!address)
      .join(', '),

    getAccountAddress: (account) => getAddressProperties('bankAddress')
      .map((prop) => account[prop])
      .concat(filter('country')(account.bankCountry))
      .filter(notEmptyFilter)
      .join(', '),

    getAccountBank: (account) => {
      const bankCode = [account.BIC, account.bankCode]
        .filter(notEmptyFilter)
        .join(' - ');

      return [
        account.bankName,
        bankCode ? `(${bankCode})` : null,
      ]
        .filter(notEmptyFilter)
        .join(' ');
    },

    /**
     * @description
     * Checkes if actual pagination type matches the one, defined in properties
     *
     * @public
     * @name isPaginationTypeMatch
     * @type {function}
     * @param {function} $ctrl Current controller
     * @param {string} type Description of pagination type (pagination, load-more)
     *
     * @returns {boolean}
     */
    isPaginationTypeMatch: ($ctrl, type) => $ctrl.state.pageParams.paginationType === type,

    /**
     * @name filterNotification
     *
     * @description
     * Helper to filter out notification from the list
     *
     * @param {object} notification
     * @param {array} notificationsList
     * @type {function}
     * @returns {null}
     */
    filterNotification: (notification, notificationsList) =>
      notificationsList.filter((item) => item !== notification),
  };
};

export default helpers;



